import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.binayshaw7777.droidfest24kobweb.pages.HomePage() }
        ctx.router.register("/about") { com.binayshaw7777.droidfest24kobweb.pages.AboutPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.HeroContainerStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.HeadingFontStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.HeroImageStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.SecondaryFontStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.HeadlineTextStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.SubheadlineTextStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.components.layouts.MarkdownStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.components.layouts.PageContentStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.components.sections.FooterStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.droidfest24kobweb.components.sections.NavHeaderStyle)
        ctx.theme.registerComponentVariants(com.binayshaw7777.droidfest24kobweb.CircleButtonVariant)
        ctx.theme.registerComponentVariants(com.binayshaw7777.droidfest24kobweb.UncoloredButtonVariant)
        ctx.stylesheet.registerKeyframes(com.binayshaw7777.droidfest24kobweb.components.sections.SideMenuSlideInAnim)
        com.binayshaw7777.droidfest24kobweb.initColorMode(ctx)
        com.binayshaw7777.droidfest24kobweb.initSiteStyles(ctx)
        com.binayshaw7777.droidfest24kobweb.initTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "DroidFest24-Kobweb"))
    renderComposable(rootElementId = "root") {
        com.binayshaw7777.droidfest24kobweb.AppEntry {
            router.renderActivePage { renderWithDeferred { it() } }
        }
    }
}
